@charset "utf-8";

/* reset */

html, body{height:100%;}

html, body, div, h1, h2, h3, h4, h5, h6, p, ol, ul, li, dl, dt, dd{margin:0;padding:0;}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section{display:block;}

body{overflow-y:hidden;font-family:Roboto,sans-serif;font-size:14px;color:#444;line-height:1.4;}

ol, ul{list-style:none;}

strong{font-weight:normal;}

em{font-style:normal;}

a{text-decoration:none;color:#3c8dbc;}

img{border:0;vertical-align:top;}

table{width:100%;border-collapse:collapse;}

caption{width:1px;height:1px;overflow:hidden;margin-bottom:-1px;text-indent:-9999px;z-index:-1;}

/* layout */

.page-wrap{height:100%;}

header{position:fixed;top:0;left:0;width:100%;height:50px;background:#367fa9;z-index:2;}

header h1{width:230px;height:47px;padding-top:3px;text-align:center;}

header h1 a{font-size:20px;font-weight:700;color:#fff;}

header h1 a span{font-weight:300;}

header .btn-log{position:absolute;top:15px;right:20px;color:#fff;}

header .m-menu{display:none;}

header .user{position:absolute;top:8px;right:100px;}

header .user .info{color:#fff;}

header .user a{margin-left:10px;}

.container{height:100%;margin-bottom:-60px;background:#fff;}

.container:after{content:"";display:block;clear:both;}

.container aside{float:left;width:230px;height:100%;padding:50px 0 60px;background:#222d32;-webkit-box-sizing:border-box;box-sizing:border-box;}

.container aside nav{height:100%;overflow-y:auto;}

.container aside nav .btn-close{display:none;}

.container aside nav .btn-cate{display:block;position:relative;margin:15px 25px 10px;padding:10px 0 10px 40px;border-radius:20px;text-shadow:0 0 1px #367fa9;color:#fff;}

.container aside nav .btn-cate:before{content:"";position:absolute;top:50%;left:20px;width:10px;height:2px;margin-top:-1px;background:#fff;}

.container aside nav .btn-cate:after{content:"";position:absolute;top:50%;left:24px;width:2px;height:10px;margin-top:-5px;background:#fff;}

.container aside nav li a{display:block;position:relative;padding:12px 10px 12px 25px;color:#b8c7ce;}

.container aside nav li.active a, .container aside nav li a:hover{color:#fff;}

.container aside nav li.active a:after, .container aside nav li a:hover:after{content:"";position:absolute;top:50%;right:10px;width:4px;height:4px;margin-top:-1px;border-right:1px solid #fff;border-bottom:1px solid #fff;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);}

.container .content{float:left;width:calc(100% - 230px);height:100%;padding:50px 0 60px;-webkit-box-sizing:border-box;box-sizing:border-box;}

.container .content .content-in{height:100%;overflow-y:auto;padding:30px 25px 40px;-webkit-box-sizing:border-box;box-sizing:border-box;}

footer{position:fixed;width:100%;height:60px;padding:20px 20px 0;background:#ecf0f5;border-top:1px solid #d2d6de;font-size:12px;color:#444;-webkit-box-sizing:border-box;box-sizing:border-box;z-index:2;}

footer .foot-msg{float:right;}

footer .copyright{float:left;}

footer .copyright a{color:#3c8dbc;}

@media screen and (max-width:767px) {
    body{overflow-y:auto;background:#ecf0f5;}
    header{position:relative;height:auto;}
    header h1{width:auto;}
    header h1 a{display:inline-block;font-size:16px;}
    header .btn-log{top:19px;right:10px;font-size:12px;color:#fff;}
    header .m-menu{display:inline-block;position:absolute;top:17px;left:10px;width:26px;height:17px;padding-top:8px;-webkit-box-sizing:border-box;box-sizing:border-box;}
    header .m-menu span{display:inline-block;width:26px;height:1px;overflow:hidden;background:#fff;text-indent:-9999px;vertical-align:top;}
    header .m-menu span:before{content:"";display:block;position:absolute;top:0;left:5px;width:16px;height:1px;background:#fff;}
    header .m-menu span:after{content:"";display:block;position:absolute;bottom:0;left:5px;width:16px;height:1px;background:#fff;}
    header .user{position:relative;top:0;right:0;padding:5px 10px;background:#3c8dbc;text-align:right;-webkit-box-sizing:border-box;box-sizing:border-box;}
    .container{height:auto;margin-bottom:0;padding-left:0;}
    .container aside{position:fixed;top:0;left:0;width:260px;left:-100%;padding:0;z-index:10;}
    .container aside:after{content:"";display:none;position:fixed;top:0;left:0;width:100%;height:100%;background:#000;z-index:10;opacity:0;}
    .container aside.visible:after{display:block;opacity:.8;}
    .container aside nav{position:relative;padding-top:50px;-webkit-box-sizing:border-box;box-sizing:border-box;z-index:11;}
    .container aside nav .btn-close{display:inline-block;position:absolute;top:13px;right:10px;width:24px;height:24px;overflow:hidden;text-indent:-9999px;}
    .container aside nav .btn-close:before{content:"";display:block;position:absolute;top:0;left:0;width:34px;height:1px;background:#fff;-webkit-transform:rotate(45deg);transform:rotate(45deg);-webkit-transform-origin:0 0;transform-origin:0 0;}
    .container aside nav .btn-close:after{content:"";display:block;position:absolute;bottom:0;left:0;width:34px;height:1px;background:#fff;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);-webkit-transform-origin:0 0;transform-origin:0 0;}
    .container .content{float:none;width:100%;min-height:170px;padding:0;}
    .container .content .content-in{padding:20px 10px 30px;}
    footer{position:relative;bottom:0;height:50px;padding:15px 10px 0;text-align:center;}
    footer .foot-msg{display:none;}
    footer .copyright{float:none;font-size:12px;}
}

/* common */

.mt10{margin-top:10px;}

.mt20{margin-top:20px;}

.mt30{margin-top:30px;}

.mt40{margin-top:40px;}

.mt50{margin-top:50px;}

.tal{text-align:left;}

.tac{text-align:center;}

.tar{text-align:right;}

.flex-box{display:-webkit-box;display:-ms-flexbox;display:flex;}

.flex-auto{-webkit-box-flex:1;-ms-flex:auto;flex:auto;}

.flex-none{-webkit-box-flex:0;-ms-flex:none;flex:none;}

.page-tit{margin-bottom:20px;font-size:24px;font-weight:400;}

.page-tit .desc{display:inline-block;margin-left:6px;font-size:14px;font-weight:100;color:#777;}

.tit-type01{position:relative;margin:20px 0 10px;padding-left:12px;font-size:18px;font-weight:400;}

.tit-type01:after{content:"";position:absolute;top:12px;left:0;width:6px;height:1px;background:#444;}

.tit-type01:first-child{margin-top:0;}

.btn{display:inline-block;position:relative;border:0;text-align:center;font-weight:400;color:#fff;vertical-align:middle;-webkit-box-sizing:border-box;box-sizing:border-box;overflow:hidden;cursor:pointer;}

.btn:focus{outline:none;}

.btn:hover:after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:#fff;-webkit-box-shadow:inset 0 0 1px #000;box-shadow:inset 0 0 1px #000;opacity:.1;}

.btn.small{height:24px;padding:3px 10px 3px;border-radius:2px;font-size:12px;}

.btn.middle{height:34px;padding:7px 12px 7px;border-radius:3px;font-size:13px;}

.btn.large{height:40px;padding:9px 15px 9px;border-radius:4px;font-size:14px;}

.btn.bg-white{border:1px solid #ddd;}

.txt-btn:hover{text-decoration:underline;}

.tab{margin-bottom:20px;border-bottom:1px solid #ddd;}

.tab:after{content:"";display:block;clear:both;}

.tab li{float:left;margin-bottom:-1px;}

.tab li + li{margin-left:2px;}

.tab li a{display:block;height:40px;padding:10px 15px 0;border:1px solid transparent;border-bottom:0;color:#555;-webkit-box-sizing:border-box;box-sizing:border-box;}

.tab li.active a{background:#fff;border:1px solid #ddd;border-bottom:0;border-radius:4px 4px 0 0;color:#3c8dbc;}

.tab.type02{border:0;}

.tab.type02 li{margin-bottom:0;}

.tab.type02 li a{height:32px;padding:5px 8px;border:0;font-size:16px;}

.tab.type02 li.active a{border-bottom:2px solid #185482;color:#185482;}

.tab-wrap{padding:50px 68px 60px;background:#fff;}

.tbl{overflow:hidden;border-bottom:1px solid #f4f4f4;}

.tbl + .tbl{margin-top:20px;}

.tbl table{width:calc(100% + 1px);margin-left:-1px;}

.tbl table thead th{padding:8px 10px;background:#d4d4d4;border-left:1px solid #f4f4f4;font-weight:400;color:#333;}

.tbl table tbody th{padding:8px 10px;background:#f4f4f4;border-top:1px solid #d4d4d4;border-left:1px solid #d4d4d4;text-align:left;color:#333;vertical-align:top;}

.tbl table tbody td{padding:8px 10px;background:#fff;border-top:1px solid #d4d4d4;border-left:1px solid #d4d4d4;color:#666;}

.tbl table tbody tr:last-child th, .tbl table tbody tr:last-child td{border-bottom:1px solid #d4d4d4;}

.tbl table tbody td.nodata{padding:50px 0;text-align:center;}

.box{padding:20px 15px;background:#fff;border:1px solid #d2d6de;border-radius:3px;-webkit-box-shadow:0 1px 1px #d2d6de;box-shadow:0 1px 1px #d2d6de;}

.box + .box{margin-top:15px;}

.accordian dl dt:first-child{margin-top:0;}

.accordian dl dt{margin-top:3px;}

.accordian dl dt a{display:block;position:relative;padding:10px 30px 10px 15px;background:#fff;border:1px solid #c1c1c1;border-radius:0 5px 5px 5px;-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-box-shadow:1px 1px 1px #d4d4d4;box-shadow:1px 1px 1px #d4d4d4;}

.accordian dl dt a:after{content:"";display:block;position:absolute;top:50%;right:13px;width:8px;height:8px;margin-top:-8px;border-left:1px solid #333;border-bottom:1px solid #333;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);-webkit-transition:all .2s;transition:all .2s;}

.accordian dl dt.on a:after{margin-top:-2px;-webkit-transform:rotate(135deg);transform:rotate(135deg);-webkit-transition:all .2s;transition:all .2s;}

.accordian dl dd{display:none;padding:15px 15px 30px;}

.form-group + .form-group{margin-top:12px;}

.form-group label, .form-group .label{display:inline-block;margin-bottom:5px;}

.form-area{display:-webkit-box;display:-ms-flexbox;display:flex;}

.form-area > *{-webkit-box-flex:1;-ms-flex:auto;flex:auto;}

.form-area > * + *{margin-left:4px;}

.form-area .btn{-webkit-box-flex:0;-ms-flex:none;flex:none}

table .form-area{margin:-3px 0;}

table .form-area + .form-area{margin-top:6px;}

table .form-area + .check-area{margin-top:6px;}

input[type=text], input[type=password]{display:inline-block;width:100%;height:34px;padding:0 9px;border:1px solid #d5d6d8;border-radius:0;font-weight:400;font-family:Roboto;font-size:13px;color:#666;-webkit-box-sizing:border-box;box-sizing:border-box;}

input[type=text]:focus, input[type=password]:focus{border-color:#4472c4;outline:none;}

input[type=text][readonly="readonly"],
input[type=password][readonly="readonly"],
input[type=text][disabled="disabled"],
input[type=password][disabled="disabled"],
textarea[readonly="readonly"],
textarea[disabled="disabled"]{background:#f4f4f4;border:1px solid #d5d6d8;color:#666;}

.select-wrap{display:inline-block;position:relative;width:100%;}

.select-wrap:after{content:"";display:block;position:absolute;top:10px;right:13px;width:8px;height:8px;border-left:2px solid #333;border-bottom:2px solid #333;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);}

select{display:block;width:100%;height:34px;padding:0 34px 0 9px;background:#fff;border:1px solid #d5d6d8;border-radius:0;font-weight:400;font-family:Roboto;font-size:13px;color:#444;outline:none;;;-webkit-appearance:none;}

select:disabled{background:#f4f4f4;border:1px solid #d5d6d8;color:#666;}

.select{position:relative;height:34px;}

.select .txt{display:block;width:100%;height:34px;padding:0 34px 0 9px;background:#fff;border:1px solid #d5d6d8;text-align:left;font-size:13px;line-height:32px;-webkit-box-sizing:border-box;box-sizing:border-box;cursor:pointer;outline:none;}

.select .txt:disabled{background:#f4f4f4;border:1px solid #d5d6d8;color:#666;cursor:inherit;}

.select .txt:after{content:"";display:block;position:absolute;top:10px;right:13px;width:8px;height:8px;border-left:2px solid #333;border-bottom:2px solid #333;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);}

.select ul{display:none;position:absolute;top:33px;left:0;width:100%;overflow:hidden;border:1px solid #d5d6d8;background:#fff;-webkit-box-sizing:border-box;box-sizing:border-box;z-index:10;}

.select ul li a{display:block;padding:6px 34px 6px 10px;font-size:13px;color:#333;}

.select ul li a:hover{background:#f4f4f4;color:#3c8dbc;}

.input-chk{display:inline-block;vertical-align:middle;}

.input-chk + .input-chk{margin-left:10px;}

.input-chk input{position:absolute;left:-9999px;}

.input-chk .txt{display:inline-block;position:relative;padding-left:24px;color:#3a3a3a;line-height:20px;cursor:pointer;}

.input-chk .txt:after{content:"";position:absolute;top:0;left:0;width:20px;height:20px;border:1px solid #d5d6d8;-webkit-box-sizing:border-box;box-sizing:border-box;}

.input-chk input[type=radio] ~ .txt:after{border-radius:100%;}

.input-chk input:checked ~ .txt:after{content:"\2713";text-align:center;font-size:16px;font-family:Roboto;}

.input-chk input[disabled=disabled] ~.txt:after{background:#f4f4f4;}

textarea{display:inline-block;width:100%;height:80px;padding:4px 9px;background:#fff;border:1px solid #d5d6d8;font-size:13px;-webkit-box-sizing:border-box;box-sizing:border-box;resize:none;}

textarea:focus{outline:none;}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{font-weight:300;font-size:13px;color:#c7c8cc;}

input::-moz-placeholder, textarea::-moz-placeholder{font-weight:300;font-size:13px;color:#c7c8cc;}

input:-ms-input-placeholder, textarea:-ms-input-placeholder{font-weight:300;font-size:13px;color:#c7c8cc;}

select::-ms-expand{display:none;}

.layer-wrap{display:none;position:fixed;top:0;left:0;width:100%;height:100%;z-index:99;}

.layer-wrap:after{content:"";position:fixed;top:0;left:0;width:100%;height:100%;background:#000;opacity:0.7;}

.layer-popup{position:absolute;width:480px;max-height:80%;max-width:80%;background:#fff;z-index:100;-webkit-box-shadow: 1px 1px 3px #333;box-shadow: 1px 1px 3px #333;opacity:0;-webkit-transition:all 1s;transition:all 1s;}

.layer-popup.on{opacity:1;}

.layer-popup .popup-tit{min-height:50px;padding:15px 50px 13px 15px;border-bottom:1px solid #f4f4f4;font-size:18px;font-weight:500;line-height:1.2;-webkit-box-sizing:border-box;box-sizing:border-box;}

.layer-popup .cont{height:100%;overflow-y:auto;padding:15px 15px 20px;-webkit-box-sizing:border-box;box-sizing:border-box;}

.layer-popup .btn-area{padding:0 15px 10px;text-align:center;}

.layer-popup .btn-area .btn{min-width:140px;}

.layer-popup .btn-area .btn + .btn{margin-left:4px;}

.layer-popup .btn-cls{position:absolute;top:0;right:0;width:50px;height:50px;overflow:hidden;text-indent:-9999px;}

.layer-popup .btn-cls:before{content:"";position:absolute;top:50%;left:50%;width:28px;height:1px;margin:-20% 0 0 -20%;background:#333;-webkit-transform:rotate(45deg);transform:rotate(45deg);-webkit-transform-origin:left top;transform-origin:left top;}

.layer-popup .btn-cls:after{content:"";position:absolute;top:50%;left:50%;width:28px;height:1px;margin:20% 0 0 -20%;background:#333;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);-webkit-transform-origin:left top;transform-origin:left top;}

@media screen and (max-width:767px) {
    .page-tit{margin-bottom:10px;font-size:18px;}
    .page-tit .desc{display:block;margin-left:0;font-size:14px;}
    .tab{border-bottom:0}
    .tab li a{height:30px;padding:4px 10px 0;}
    .tab li.active a{border-bottom:1px solid #ddd;border-radius:4px;}
    .tab.type02 li.active a{border-radius:0;}
    .tbl.form{border:0;}
    .tbl.form table{display:block;width:100%;margin:0;}
    .tbl.form tbody{display:block;}
    .tbl.form tr{display:block;margin-top:12px;}
    .tbl.form tbody th{display:block;padding:0 0 8px 0;background:none;font-weight:400;border:0 !important;}
    .tbl.form tbody td{display:block;padding:0;background:none;border:0 !important;}
    .layer-popup .btn-area{display:-webkit-box;display:-ms-flexbox;display:flex;padding:0 15px 10px;text-align:center;}
    .layer-popup .btn-area .btn{-webkit-box-flex:1;-ms-flex:auto;flex:auto;min-width:auto;}
}

