/* login */

.login-box{position:absolute;top:50%;left:50%;width:460px;padding:30px 50px 30px;margin:-190px 0 0 -250px;background:#fff;border:1px solid #ececec;border-radius:3px;text-align:center;-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-box-shadow:0 5px 10px #c3c3c3;box-shadow:0 5px 10px #c3c3c3;-webkit-animation:loginbox 1s;animation:loginbox 1s;}

@-webkit-keyframes loginbox{
    0%{top:-100%;opacity:0;}
    100%{opacity:100%;}
}

@keyframes loginbox{
    0%{top:-100%;opacity:0;}
    100%{opacity:100%;}
}

.login-box .log-iniput{margin-top:30px;}

.login-box .log-iniput input[type=text]{height:50px;margin-bottom:5px;padding:0 20px;background:#f3f3f3;border:0;border-radius:3px;text-align:center;font-size:16px;}

.login-box .log-iniput input[type=password]{height:50px;margin-bottom:5px;padding:0 20px;background:#f3f3f3;border:0;border-radius:3px;text-align:center;font-size:16px;}

.login-box .log-iniput .btn{width:100%;height:50px;font-size:16px;}

.login-box .log-msg{margin-top:30px;font-size:16px;}

@media screen and (max-width:767px) {
    .login-box{position:absolute;top:40px;margin:0 0 0 -150px;width:300px;padding:20px 20px 20px;}
    .login-box .logo img{width:105px;}
    .login-box .log-iniput{margin-top:10px;}
    .login-box .log-iniput input[type=text]{height:40px;padding:0 10px;}
    .login-box .log-iniput input[type=password]{height:40px;padding:0 10px;}
    .login-box .log-iniput .btn{height:40px;font-size:14px;}
    .login-box .log-msg{margin-top:20px;font-size:14px;}
}